.ast-container {
    max-width: 2340px;
	padding-left: 0px;
	padding-right: 0px;
  }

.block-class{
	
	display: flex;
	justify-content: space-between;
	background-color: #e0e0e4;
	width: 100%;
	margin: 0 auto;
	height: 90px;
	position: relative;
	overflow: hidden;

}
.image-container {
    flex: 1;
    margin: 0 10px; /* Ajusta el valor del margen según el espacio que desees entre las imágenes */
    text-align: center; /* Centra la imagen en su contenedor */
}

.image-container a .image-0 {
	height: 60px;
	margin-top: 15px;
	width: 180px;
}

.image-container a .image-1 {
	height: 60px;
	margin-top: 15px;
	width: 180px;
}

.image-container a .image-2 {
	height: 60px;
	margin-top: 15px;
	width: 180px;
}
@media (max-width: 600px) {
	.block-class{
		height: 75px !important;
	}
	.image-container a .image-0,
	.image-container a .image-1,
	.image-container a .image-2 {
		height: 40px !important;
		width: 90px !important;
		margin-top: 15px !important;
	}
}
@media (max-width: 900px) {
	.block-class{
		height: 100px;
	}
	.image-container a .image-0,
	.image-container a .image-1,
	.image-container a .image-2 {
		height: 60px;
		width: 165px;
		margin-top: 15px;
	}
}

